* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.3rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
  max-width: 157px; /*Establece el tamaño máximo de los párrafos, para que el borde no sobrepase el tamaño de la imagen o el gift*/
  /*En caso de que el texto ya no quepa agrega puntos suspensivos en caso de que pasen el límite establecido*/
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.card img {
  max-height: 170px;
}